import back from '@/components/back'
import axios from 'axios'
export default {
    components:{
        back
    },
    data(){
        return {
            items: [],
            act: 1008611,
            children: [],
            province: null,
            user: this.$store.state.userInfo.Data,
        }
    },
    created(){
    	if(this.$route.query.fromService){
    		let i = {
    			AreaName: '',
                AreaId: 0
    		}
    		this.pullForService(i,false,false)
    		this.pullForService(i,true,true)
    	}else{
    		this.pullHot()
        	this.pull()
    	}
    },
    methods:{
        choose(i,idx){
            this.act = idx
            if(idx === 1008611){
            	if(this.$route.query.fromService){
            		this.pullForService(i,true,true)
            	}else{
            		this.pullHot()
            	}
            }
            else if(idx === 1001011){
                this.$store.dispatch('pusharea',{
                    AreaName: '全部地区',
                    AreaId: -1
                })
                this.$router.back()
            }
            else{
                this.province = i
                if(this.$route.query.fromService){
                	this.pullForService(i,false,true)
                }else{
                	this.pull(i.AreaId)
                }
                
            }
        },
        toAll(){
        	if(this.act == 1008611){
        		this.province = {
        			AreaName: '全部地区',
                    AreaId: -1
        		}
        	}
            this.$store.dispatch('pusharea',this.province)
            this.$router.back()
        },
        pull(ParentId){
            if(!ParentId){
                ParentId = 0
                this.$get('PlatFormAPI/Area/QueryAreaByParentId',{
                    ParentId,
                }).then(o => {
                    this.items = o
                })
            }
            else{
                this.$get('PlatFormAPI/Area/QueryAreaByParentId',{
                    ParentId,
                }).then(o => {
                    this.children = o
                })
            }
        },
        pullHot(){
            this.$get('PlatFormAPI/Area/QueryHostArea').then(o => {
                this.children = o
            })
        },
        pullForService(i,isHot,isItem){
        	//isHot:是否是热门，isItem是否是市区
        	this.$post('PlatFormAPI/Area/QueryServicePackAreaList',{
        		AreaId:i ? i.AreaId : 0,
        		AreaName:i ? i.AreaName : "",
        		IsHot:isHot?1:-1,
        		DrId: this.user.User.DoctorId,
        	}).then(o => {
        		if(isItem){
        			this.children = o
        		}else{
        			this.items = o
        		}
            })
        },
        goList(i){
            this.$store.dispatch('pusharea',i)
            this.$router.back()
        }
    }
}



